import React, { useState } from "react";

import "./Shop.css";

import arrowNext from "../../assets/arrow-left.svg";
import shoppingCart from "../../assets/shopping-cart.svg";
import removeShoppingCart from "../../assets/remove-shopping-cart.svg";

export default function Shop({ sneakers, handleCartChange }) {
  const [viewPrice, setViewPrice] = useState(1);

  const onShow = (value) => {
    setViewPrice(value);
  };

  return (
    <section className="Shop">
      <div
        key={sneakers[viewPrice - 1].id}
        // style={{ backgroundColor: sneakers[viewPrice - 1].bgColor }}
        className="Single-product"
      >
        <header>
          <h3>
            <span>{sneakers[viewPrice - 1].price}</span>
          </h3>
          {/* {sneakers[viewPrice - 1].cart === 0 ? (
            <span
              onClick={() => handleCartChange(sneakers[viewPrice - 1].id, 1)}
            >
              <img src={shoppingCart} alt="add to cart" />
            </span>
          ) : (
            <span
              onClick={() => handleCartChange(sneakers[viewPrice - 1].id, -1)}
            >
              <img src={removeShoppingCart} alt="remove to cart" />
            </span>
          )} */}
        </header>
        <figure>
          <img
            src={sneakers[viewPrice - 1].image}
            alt={sneakers[viewPrice - 1].name}
          />
        </figure>
        <footer>
          <h4>{sneakers[viewPrice - 1].name}</h4>
          <p>
            Neque porro quisquam est qui dolorem ipsum quia dolor sit amet...
          </p>
        </footer>
      </div>

      <div className="Products">
        {sneakers.length &&
          sneakers.map((sneaker, index) => {
            return (
              <div
                key={sneaker.id}
                style={{ backgroundColor: sneaker.bgColor }}
                className="Card"
                onMouseOver={() => onShow(sneaker.id)}
                onClick={() => onShow(sneaker.id)}
              >
                {viewPrice === sneaker.id && (
                  <header>
                    <h3 className="animated fadeIn slow">
                      <span>{sneaker.price}</span>
                    </h3>
                    {sneakers[viewPrice - 1].cart === 0 ? (
                      <span onClick={() => handleCartChange(index, 1)}>
                        <img src={shoppingCart} alt="add to cart" />
                      </span>
                    ) : (
                      <span onClick={() => handleCartChange(index, -1)}>
                        <img src={removeShoppingCart} alt="remove to cart" />
                      </span>
                    )}
                  </header>
                )}
                <figure
                  style={viewPrice !== sneaker.id ? { margin: "auto" } : null}
                >
                  <img src={sneaker.image} alt={sneaker.name} />
                </figure>
                {viewPrice === sneaker.id && (
                  <h4 className="animated fadeIn slow">{sneaker.name}</h4>
                )}
              </div>
            );
          })}

        <ul className="Pagination">
          <li>
            <img src={arrowNext} alt="next" />
            <span>
              <span>1</span>
              <span>/</span>
              <span>3</span>
            </span>
          </li>
          <li>
            <span>New</span>
            <span>Popular</span>
          </li>
        </ul>
      </div>
    </section>
  );
}
