import React, { useState } from "react";

import "./MainHeader.css";

import logo from "../../assets/logo.png";
import menuIcon from "../../assets/menu-icon.svg";
import closeIcon from "../../assets/close-icon.svg";
import facebook from "../../assets/facebook.svg";
import twitter from "../../assets/twitter.svg";
import linkedin from "../../assets/linkedin.svg";
import instagram from "../../assets/instagram.svg";
import skype from "../../assets/skype.svg";
import youtube from "../../assets/youtube.svg";
import { NavLink } from "react-router-dom";

export default function MainHeader() {
  const [toggleHeader, settoggleHeader] = useState(false);

  const onToggleHeader = () => {
    settoggleHeader(!toggleHeader);
  };

  const styleMenu = {
    position: "fixed",
    width: "47%",
    height: "100vh",
    padding: "2em 6em",
  };

  const styleSocial = {
    width: "50%",
    flexDirection: "row",
    paddingBottom: "2em",
  };

  const styleCloseIcon = {
    height: "24px",
    margin: "auto",
    marginLeft: "-3em",
  };

  return (
    <header
      style={!toggleHeader ? null : styleMenu}
      className={
        !toggleHeader
          ? "App-header"
          : "App-header animated slideInLeft add-class"
      }
    >
      <h1>
        <img src={logo} alt="logo" />
      </h1>
      <nav>
        {!toggleHeader ? (
          <img src={menuIcon} alt="open menu" onClick={onToggleHeader} />
        ) : (
          <>
            <img
              style={styleCloseIcon}
              src={closeIcon}
              className="close-menu"
              alt="close menu"
              onClick={onToggleHeader}
            />
            <ul>
              <li>
                {" "}
                <NavLink to="/sneaker-eshop/about" onClick={onToggleHeader}>
                  About
                </NavLink>
              </li>
              <li>
                <NavLink to="/sneaker-eshop/sneakers" onClick={onToggleHeader}>
                  Sneakers
                </NavLink>
              </li>
              <li>
                {" "}
                <NavLink
                  to="/sneaker-eshop/accessories"
                  onClick={onToggleHeader}
                >
                  Accessories
                </NavLink>
              </li>
              <li>
                <NavLink exact to="/sneaker-eshop" onClick={onToggleHeader}>
                  Store
                </NavLink>
              </li>
              <li>
                <NavLink to="/sneaker-eshop/contacts" onClick={onToggleHeader}>
                  Contacts
                </NavLink>
              </li>
            </ul>
          </>
        )}
      </nav>
      <ul
        style={!toggleHeader ? null : styleSocial}
        className={!toggleHeader ? "social-media" : "social-media add-class"}
      >
        {!toggleHeader ? (
          <span>Social Networks</span>
        ) : (
          <>
            <img src={facebook} alt="facebook" />
            <img src={twitter} alt="twitter" />
            <img src={linkedin} alt="linkedin" />
            <img src={instagram} alt="instagram" />
            <img src={skype} alt="skype" />
            <img src={youtube} alt="youtube" />
          </>
        )}
      </ul>
    </header>
  );
}
