import React, { useState } from "react";
import { Route, Switch } from "react-router-dom";
import MainHeader from "./compenents/MainHeader/MainHeader";
import MainFooter from "./compenents/MainFooter/MainFooter";
import { sneakers } from "./data/products";
import LandingPage from "./pages/LandingPage";
import ContactPage from "./pages/ContactPage/ContactPage";
import "./App.css";

function App() {
  const [products, setProducts] = useState(sneakers);

  const handleCartChange = (index, delta) => {
    setProducts([...products], {
      cart: (products[index].cart += delta),
    });
  };

  return (
    <div className="App">
      <MainHeader />
      <main className="App-main">
        <Switch>
          <Route
            path="/sneaker-eshop"
            exact
            render={(props) => (
              <LandingPage
                products={products}
                handleCartChange={handleCartChange}
              />
            )}
          />
          <Route path="/sneaker-eshop/contacts" component={ContactPage} />
        </Switch>
      </main>
      <MainFooter sneakers={products} />
    </div>
  );
}

export default App;
