import React from "react";

import "./MainFooter.css";

import search from "../../assets/search-icon.svg";
import cartIcon from "../../assets/bag.svg";

export default function MainFooter({ sneakers }) {
  const cartNumber =
    sneakers.length && sneakers.filter((sneaker) => sneaker.cart === 1).length;

  return (
    <footer className="App-footer">
      <img src={search} alt="search" />

      <figure className="cart">
        <img src={cartIcon} alt="open menu" />
        <span>{cartNumber}</span>
      </figure>

      <p>
        <span>&copy;</span> <span>{new Date().getFullYear()}</span>
      </p>
    </footer>
  );
}
